import React, { useState, useEffect } from "react";
import QRCode from "qrcode.react";
import { useReactToPrint } from "react-to-print";

const PrintTag = () => {
  const componentRef = React.useRef();
  const [selectedItems, setSelectedItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [items, setItems] = useState([]);
  const [expandedItemId, setExpandedItemId] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [printMode, setPrintMode] = useState("single"); // 'single' or 'multiple'
  const [printList, setPrintList] = useState([]); // Separate list for multiple prints
  const [searchOption, setSearchOption] = useState("Code"); // Default search option is Code
  const [exactMatch, setExactMatch] = useState(false);
  const [searchedItems, setSearchedItem] = useState(null);
  const [itemNotFound, setItemNotFound] = useState(false);
  const [searching, setSearching] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [margin, setMargin] = useState(2); // default is 2

  useEffect(() => {
    if (selectedItems.length > 1) {
      let newList = [];
      let n = 0;
      let i = -1;
      let selectedItemsCopy = [...selectedItems]; // Create a shallow copy

      while (true) {
        // Reset index if we reach the end of the list
        if (i >= selectedItemsCopy.length - 1) {
          n++;
          i = 0;
        } else {
          i++;
        }
        if (n < selectedItemsCopy[i].quantityBuy) {
          newList.push(selectedItemsCopy[i]); // Push a copy of the item
        } else {
          selectedItemsCopy.splice(i, 1); // Remove the item once its quantityBuy has been processed
          i--;
        }

        // Exit loop if all items have been processed
        if (selectedItemsCopy.length === 0) {
          break;
        }
      }

      setPrintList(newList);
    }
  }, [selectedItems]);

  function calculateMRP(mrp) {
    // Round the number to the nearest multiple of 100
    var roundedNumber = mrp * margin + 10;
    roundedNumber = Math.round(roundedNumber / 100) * 100;
    return roundedNumber - 4;
  }

  const fetchItemWithOption = async (option, query) => {
    setSearching(true);
    try {
      const response = await fetch(
        `/api/item/${
          exactMatch ? "exact-search" : "search"
        }/${option}/${query}`,
        {
          headers: {
            Authorization: `Bearer ${
              JSON.parse(window.localStorage.getItem("userInfo")).token
            }`,
          },
        }
      );
      if (response.ok) {
        const data = await response.json();
        setSearchedItem(data); // Clear searchedItem state
        setItemNotFound(false); // Reset itemNotFound state
      } else if (response.status === 401) {
        window.localStorage.clear();
        setSearchedItem(null); // Clear searchedItem state
        setItemNotFound(true); // Set itemNotFound state to true
        setSearching(false);
        // naviagate("/login");
      } else {
        setSearchedItem(null); // Clear searchedItem state
        setItemNotFound(true); // Set itemNotFound state to true
        setSearching(false);
        // console.error("Failed to fetch items");
      }
    } catch (error) {
      // console.log(error);
    } finally {
      setSearching(false);
    }
  };

  // Fetch items function similar to History component
  const fetchItems = async () => {
    setLoading(true);
    try {
      const response = await fetch(`/api/item/paginate?page=${page}&limit=10`, {
        headers: {
          Authorization: `Bearer ${
            JSON.parse(window.localStorage.getItem("userInfo")).token
          }`,
        },
      });
      if (response.ok) {
        const data = await response.json();
        if (page === 1) {
          setItems(data.items);
        } else {
          setItems((prevItems) => [...prevItems, ...data.items]);
        }
      } else {
        console.error("Failed to fetch items");
      }
    } catch (error) {
      console.error("Error fetching items:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchItems();
    // eslint-disable-next-line
  }, [page]);

  // Function to handle selecting an item
  const handleSelectItem = (item) => {
    setSelectedItems((prevSelectedItems) => {
      const itemIndex = prevSelectedItems.findIndex(
        (selectedItem) => selectedItem._id === item._id
      );
      if (itemIndex !== -1) {
        // Item exists, remove it from the selected items list
        return prevSelectedItems.filter(
          (selectedItem) => selectedItem._id !== item._id
        );
      } else {
        // Item does not exist, add it to the selected items list
        return [...prevSelectedItems, item];
      }
    });
  };

  // Function to handle printing selected items
  const handlePrintButton = () => {
    setIsModalOpen(true);
  };

  // Function to load more items
  const loadMoreItems = () => {
    setPage((prevPage) => prevPage + 1);
  };
  const toggleExpand = (itemId) => {
    setExpandedItemId((prevId) => (prevId === itemId ? null : itemId));
  };
  // Function to print the component
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  });

  const handleSearch = (e) => {
    const inputValue = e.target.value;
    const alphanumericRegex = /^[a-zA-Z0-9\s]*$/;

    if (!alphanumericRegex.test(inputValue)) {
      // If the input contains special characters, do nothing
      return;
    }

    setSearchQuery(inputValue.toUpperCase());

    if (inputValue === "") {
      setSearchedItem(null);
      setItemNotFound(false);
    } else {
      fetchItemWithOption(searchOption, inputValue);
    }
  };

  useEffect(() => {
    fetchItemWithOption(searchOption, searchQuery);
    // eslint-disable-next-line
  }, [searchOption, exactMatch]);

  const handleSearchOptionChange = (e) => {
    setSearchOption(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (searchQuery.trim() === "") {
      setSearchedItem(null);
      setItemNotFound(false);
    } else {
      fetchItemWithOption(searchOption, searchQuery);
    }
    setSearchQuery("");
  };

  const handleToggleExactMatch = (e) => {
    e.preventDefault();
    setExactMatch((prevExactMatch) => !prevExactMatch); // Toggle exact match state
  };

  return (
    <div>
      <h1 className="text-2xl font-semibold mb-4">Print Tags</h1>
      <form onSubmit={handleSubmit} className="mb-4 flex items-center">
        <select
          value={searchOption}
          onChange={handleSearchOptionChange}
          className="border border-gray-300 rounded-l px-4 py-2 w-1/4"
        >
          <option value="Code">Code</option>
          <option value="Brand">Brand</option>
          <option value="Product">Product</option>
          <option value="Category">Category</option>
          <option value="Size">Size</option>
          <option value="mrp>=">MRP Gt</option>
          <option value="mrp<=">MRP Ls</option>
        </select>
        <input
          type="text"
          value={searchQuery}
          onChange={handleSearch}
          placeholder="Search by code"
          className="border border-gray-300 rounded-l px-4 py-2 w-2/4"
        />
        <button
          type="submit"
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-r w-1/4"
        >
          {searching ? "Serahcing" : "Search"}
        </button>
        {/* Toggle button for exact match search */}
      </form>
      <div className="flex justify-center">
        <button
          onClick={handleToggleExactMatch}
          className={`bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded ${
            exactMatch ? "bg-blue-500 hover:bg-blue-700 text-white" : ""
          }`}
        >
          {exactMatch ? "Exact Match Search On" : "Exact Match Search Off"}
        </button>
        <input
          type="number"
          step="0.1"
          placeholder="Enter Margin (e.g. 1.8)"
          value={margin}
          onChange={(e) => setMargin(parseFloat(e.target.value) || 1)}
          onWheel={(e) => e.target.blur()}
          className="w-full mt-4 p-2 border rounded-md"
        />
      </div>

      {searching && (
        <p className="text-green-500 text-center font-semibold"> Searching</p>
      )}

      {searchedItems ? (
        <div>
          <h2 className="text-xl font-semibold mb-2">Searched Item</h2>
          <p>Number of result = {searchedItems.length}</p>
          {searchedItems.map((item) => (
            <div
              key={item._id}
              className={`bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded relative ${
                item.quantityBuy - item.quantitySold <= 0
                  ? "bg-red-100 border border-red-400 text-red-700"
                  : ""
              }`}
            >
              <div
                className="flex justify-between items-center cursor-pointer"
                onClick={() => toggleExpand(item._id)}
              >
                <div>
                  <p className="font-semibold text-lg">Code: {item.code}</p>
                  <p className="text-black">Brand: {item.brand}</p>
                </div>
                <div>
                  <input
                    type="checkbox"
                    checked={selectedItems.includes(item)}
                    onChange={() => handleSelectItem(item)}
                    className="mr-2"
                  />
                  <svg
                    className={`h-6 w-6 ${
                      expandedItemId === item._id ? "transform rotate-180" : ""
                    }`}
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 9l-7 7-7-7"
                    />
                  </svg>
                </div>
              </div>
              {expandedItemId === item._id && (
                <div
                  className={`flex items-center justify-evenly border-t border-gray-500 mt-4 pt-4 ${
                    item.quantityBuy - item.quantitySold <= 0
                      ? "bg-red-100  text-red-700"
                      : ""
                  }`}
                >
                  <div className="text-black">
                    <p className="font-semibold py-1">
                      Product: {item.product}
                    </p>
                    <p className="font-semibold py-1">
                      Category: {item.category}
                    </p>
                    <p className="font-semibold py-1">Size: {item.size}</p>
                    <p className="font-semibold py-1">
                      Quantity Available: {item.quantityBuy - item.quantitySold}
                    </p>
                    <p className="font-semibold py-1">
                      Quantity Sold: {item.quantitySold}
                    </p>
                    <p className="font-semibold py-1">MRP: {item.mrp}</p>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      ) : (
        itemNotFound && (
          <div className="mt-2 p-2 bg-red-500 text-white text-center rounded-md">
            No product found with this code.
          </div>
        )
      )}

      <hr className="my-8 border-t border-gray-300" />
      <div className="flex justify-center">
        <button
          onClick={handlePrintButton}
          disabled={selectedItems.length === 0}
          className={`bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mt-1 mr-2 ${
            selectedItems.length === 0 ? "opacity-50 cursor-not-allowed" : ""
          }`}
        >
          Print Selected
        </button>
      </div>
      {isModalOpen && (
        <>
          <div
            className="fixed inset-0 flex items-center justify-center"
            style={{ backgroundColor: "rgba(0, 0, 0, 0.5)", zIndex: 999 }}
          >
            <div
              className="bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded relative w-full m-5"
              role="alert"
              contentEditable
            >
              <div className="max-h-[80vh] overflow-y-auto hide-scrollbar">
                <strong className="font-bold">Items added to inventory:</strong>
                <div className="flex justify-center m-4">
                  <button
                    onClick={() => setPrintMode("single")}
                    className={`px-4 py-2 mx-2 ${
                      printMode === "single" ? "bg-indigo-700" : "bg-indigo-300"
                    } text-white rounded-md focus:outline-none`}
                  >
                    Print Single Tags
                  </button>
                  <button
                    onClick={() => setPrintMode("multiple")}
                    className={`px-4 py-2 mx-2 ${
                      printMode === "multiple"
                        ? "bg-indigo-700"
                        : "bg-indigo-300"
                    } text-white rounded-md focus:outline-none`}
                  >
                    Print Multiple Tags
                  </button>
                </div>
                <div className="flex justify-center">
                  <div
                    style={{ width: "106mm" }}
                    ref={componentRef}
                    className="flex flex-wrap"
                  >
                    {selectedItems.length % 2 !== 0 &&
                      selectedItems.push(
                        selectedItems[selectedItems.length - 1]
                      )}
                    {(printMode === "single" ? selectedItems : printList).map(
                      (item, index) => (
                        <div
                          key={index}
                          className="flex items-center justify-evenly"
                        >
                          <div
                            style={{
                              width: "50mm",
                              height: "25mm",
                              // border: "1px solid black",
                              padding: 0,
                              marginLeft: "1.3mm",
                              marginRight: "1.3mm",
                            }}
                            className="flex justify-evenly bg-white"
                          >
                            <div className="p-2 pr-0">
                              <QRCode value={item.code} size={60} />
                              <p className="text-black">{item.code}</p>
                            </div>
                            <div className="relative font-bold text-black pt-1">
                              <div className="flex justify-center">
                                <img
                                  src={`Images/Ganesha.jpg`}
                                  alt="project"
                                  width={"25mm"}
                                />
                              </div>
                              <div>
                                <p>MRP: {calculateMRP(item.mrp)}/-</p>
                                <p>Size: {item.size}</p>
                                <div>
                                  <p className="absolute right-1 bottom-1 text-sm font-normal">
                                    {item.secretCode}
                                  </p>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      )
                    )}
                  </div>
                </div>
                <button
                  onClick={handlePrint}
                  disabled={selectedItems.length === 0}
                  className={`w-full py-2 px-4 rounded focus:outline-none focus:shadow-outline-indigo active:bg-indigo-800 ${
                    selectedItems.length !== 0
                      ? "bg-indigo-500 text-white"
                      : "bg-gray-300 text-black opacity-50 cursor-not-allowed"
                  }`}
                >
                  Print
                </button>
                <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
                  <svg
                    className="fill-current h-6 w-6 text-green-500 cursor-pointer"
                    role="button"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                    onClick={() => setIsModalOpen(false)}
                  >
                    <title>Close</title>
                    <path d="M14.348 14.849a1.2 1.2 0 0 1-1.697 0L10 11.819l-2.651 3.029a1.2 1.2 0 1 1-1.697-1.697l2.758-3.15-2.759-3.152a1.2 1.2 0 1 1 1.697-1.697L10 8.183l2.651-3.031a1.2 1.2 0 1 1 1.697 1.697l-2.758 3.152 2.758 3.15a1.2 1.2 0 0 1 0 1.698z" />
                  </svg>
                </span>
              </div>
            </div>
          </div>
        </>
      )}
      <div>
        {items.map((item) => (
          <div key={item._id}>
            <div
              key={item._id}
              className="bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded relative"
            >
              <div
                className="flex justify-between items-center cursor-pointer"
                onClick={() => toggleExpand(item._id)}
              >
                <div>
                  <p className="font-semibold text-lg">Code: {item.code}</p>
                  <p className="text-black">Brand: {item.brand}</p>
                </div>
                <div>
                  <input
                    type="checkbox"
                    checked={selectedItems.includes(item)}
                    onChange={() => handleSelectItem(item)}
                  />
                  <svg
                    className={`h-6 w-6 ${
                      expandedItemId === item._id ? "transform rotate-180" : ""
                    }`}
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                  >
                    {/* <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 9l-7 7-7-7"
                    /> */}
                  </svg>
                </div>
              </div>
              {expandedItemId === item._id && (
                <div className="flex items-center justify-evenly border-t border-gray-300 mt-4 pt-4">
                  <div className="text-black">
                    <p className="font-semibold py-1">
                      Product: {item.product}
                    </p>
                    <p className="font-semibold py-1">
                      Category: {item.category}
                    </p>
                    <p className="font-semibold py-1">Size: {item.size}</p>
                    <p className="font-semibold py-1">
                      Quantity Available: {item.quantityBuy - item.quantitySold}
                    </p>
                    <p className="font-semibold py-1">MRP: {item.mrp}</p>
                  </div>
                </div>
              )}
            </div>
          </div>
        ))}
      </div>

      <div className="flex justify-center">
        <button
          onClick={loadMoreItems}
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mt-1"
        >
          {loading ? "Loading..." : "Load More"}
        </button>
      </div>
    </div>
  );
};

export default PrintTag;
